  display: flex;
}

.country-list__item {
  align-items: center;
  margin-bottom: 10px;
  font-size: 18px;
  display: flex;
}

.country-list__image {
  width: 75px;
  height: auto;
  margin-right: 10px;
  display: block;
}

.country-info {
